import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-reactive-form',
  templateUrl: './reactive-form.component.html',
  styleUrls: ['./reactive-form.component.css']
})
export class ReactiveFormComponent implements OnInit {
  formReactive: FormGroup;
  constructor(private fb: FormBuilder) { 
  }

  ngOnInit() {
    this.formReactive = this.fb.group({  //new FormGroup when not use FormBuilder
      id: 1,                             //new FormControl('1')
      username: ['', gmailValidator],                //new FormControl('mclam')
      password: 'mclam123',             //new FormControl('mclam123')
      subject: this.fb.group({
        football: false,               //new FormControl(false)
        angular: true,                 //new FormControl(true)
        nodejs: false                   //new FormControl(false)
      })
   })
  }

  onSubmit(){
    console.log(this.formReactive.value);
  }
}

function gmailValidator(formControl: FormControl){
    if(formControl.value.includes('@gmail.com')){
      return null;
    }else {
      gmail: true;
    }
}
